import React from 'react'
import ReactDOM from 'react-dom'
import $ from 'jquery'
import Tweet from './Tweet'

class Main extends React.Component {
    render() {
        return (
            <Tweet />
        );
    }
}

let documentReady = () => {
    ReactDOM.render(
        <Main />,
        document.getElementById('root')
    );
};

$(documentReady);